/*override*/


.App {
    height: 100vh;
}

.custom-shadow{

    box-shadow: 0 0 5px 0.1px rgba(255, 255, 255, 0.4);
}
.comment {
    color:  rgb(51, 95, 51);
}
.btn-connect {
    background: none;
   padding:0.5rem;
    border-radius: 24px;
    color:white;
    border: 1px solid white;
    transition: background-color 0.5s linear ;
}
.btn-connect:hover{
    background-color: rgb(0, 162, 255);
    color: black;
}
.custom-cursor-txt:hover {
    cursor: url('../public/cursor.png'), auto; /* Custom cursor on hover */

}
.custom-cursor-txt{
    position: relative;
}
.custom-cursor-txt::before {
    content: '';
    top:50%;
    position: absolute;
    height: 4px;
    width: 100%;
    background-color: white;
}
.custom-cursor-txt:hover::after{
    content: 'Not so Experienced';

    padding:0.4rem 2rem;

    border-radius: 24px;
    font-size: 16px;
    position: absolute;
    top: -30%;
    right: -50%;
    color:yellow;
}

.circle{
   
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: white;
    animation : circleAnimation 3s infinite;
}

@keyframes circleAnimation {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(-1);
    }
}

.brief{
    margin: auto;
    width: 30%;
}

@media (max-width: 1400px) {
    .links,.contact { 
        display: none;
    }
}


.float {
    position: absolute;
    object-fit: contain;
  /*  background-color: white;*/
    left:-10%;
    border-radius:50%;
    width: 50px;
    top:50%;
    height: 50px;

    animation: floatAnimation 10s ease infinite;
}
.f1 {
animation-delay: 5s;
}
.f2 {
    animation-delay: 1s;
}
.f3 {
    animation-delay: 3s;
}
.f4 {
    animation-delay:4s;
}


@keyframes floatAnimation {
    0% {
        transform: rotate(0deg) translate(50px, -50px);
    }
    40% {
        transform: rotate(40deg) translate(800px,-400px);
    }
    70% {
        transform: rotate(0deg) translate(10px,200px);
    }
    100% {
        transform: rotate(-0deg) translate(50px, -50px);
    }
}
